'use strict'

import React from "react";

import "./footer-style.css";

export default class Footer extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {

        return (
            <footer
                style= {
                    { backgroundImage: `url(${this.props.img})` }
                }
            >

            </footer>
        );
    }
}