'use strict'

import React from "react";

import "./body-style.css";

export default class Body extends React.Component {

    constructor(props) {
        super(props);
        this.data = this.props.data;
        this.createList = this.createList.bind(this);
    }

    createList(data) {
        let res = [];

        res = data.map( (elem, index)=>{

            let list = elem.list.map( (li, index, list)=>{
                return (
                    <li 
                        id={"c_" + (index+1)}
                        key={"c_" + (index+1)}
                    >
                        {list[index]}
                    </li>
                );
            } );

            return (
                <ul
                    id={index+1}
                    key={index+1}
                    className="meal"
                >
                    <li>{elem.name}</li>
                    <ul>{list}</ul>
                </ul>
            );
        } );

        return res;
    }

    render() {
        
        return(
            <div className="body">
                <main>
                    <ul className="menu">
                        {this.createList(this.data)}
                    </ul>
                </main>
            </div>
        );
    }
}