'use strict'

import React from "react";

import "./header-style.css";

export default class Header extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {

        const style = {
            backgroundImage: `url(${this.props.img})`,
        }



        return(
            <header style={style} >
                
            </header>
            
        );
    }
}