'use strict'

import React from "react";

import Header from "./Header/Header.js";
import Body from "./Body/Body.js";
import Footer from "./Footer/Footer.js";

import imgSmall from "../../img/palme.png";
import imgBig from "../../img/palme-int.png";


export default class App extends React.Component {

    constructor(props) {
        super(props);
        this.refreshImg = this.refreshImg.bind(this);
        this.img = null;
        this.state = { width: 0 };
        this.data = this.props.data ? this.props.data : false;
    }

    componentDidMount() {
        this.setState({
            width : window.innerWidth
        });

        window.addEventListener( "resize", this.refreshImg );
    }

    componentWillUnmount() {
        window.removeEventListener( "resize", this.refreshImg )
    }

    refreshImg() {
        this.setState({
            width : +window.innerWidth
        });
    }

    render() {

        this.img = (this.state.width > 516) ? imgBig : imgSmall;

        return(
            <div>
                <Header img={ this.img } />
                <Body 
                    data={ this.data } 
                />
                <Footer img={ this.img }/>
            </div>
        );}
}